async function openExtensionPopup(): Promise<void> {
  try {
    if ('openPopup' in browser_api.action) {
      const window = await browser_api.windows.getCurrent();
      if (window.id) {
        await browser_api.windows.update(window.id, { focused: true });
      }
      await browser_api.action.openPopup({ windowId: window.id });
    }
  } catch (err) {
    console.warn(err);
  }
}

async function setUninstallUrl(userId: string) {
  const onDeleteUrl = await getConfigsUrl(process.env.ON_DELETE_URL);

  if (onDeleteUrl) {
    if (userId) {
      onDeleteUrl.searchParams.set('clientId', userId);
    }

    // browser_api.runtime.setUninstallURL(onDeleteUrl.toString(), () => {
    //   if (browser_api.runtime.lastError) {
    //     console.warn(
    //       'Failed to set uninstall URL:',
    //       browser_api.runtime.lastError,
    //     );
    //   }
    // });
    await browser_api.runtime.setUninstallURL(onDeleteUrl.toString());
    if (browser_api.runtime.lastError) {
      console.warn(
        'Failed to set uninstall URL:',
        browser_api.runtime.lastError,
      );
    }
  }
}

browser_api.runtime.onMessage.addListener(async (message: unknown) => {
  if (!isMessageOfType(message, Actions.OpenPopup)) return;
  await openExtensionPopup();

  browser_api.runtime.onMessage.addListener(async function listener(
    msg: unknown,
  ) {
    const { userId } = await getLocalStoragePremiumData(
      LocalStoragePremiumKey.UserId,
    );

    if (!isMessageOfType(msg, Actions.PopupReady) || !userId) return;

    await browser_api.runtime.sendMessage<NavigateToFreeTrialMessage>({
      action: Actions.NavigateToFreeTrial,
    });
    browser_api.runtime.onMessage.removeListener(listener);
  });
});

browser_api.runtime.onMessage.addListener(async (message: unknown) => {
  if (!isMessageOfType(message, Actions.OpenPopupPayment)) return;
  await openExtensionPopup();

  browser_api.runtime.onMessage.addListener(async function listener(
    msg: unknown,
  ) {
    if (!isMessageOfType(msg, Actions.PopupReady)) return;

    if (msg.action === Actions.PopupReady) {
      await browser_api.runtime.sendMessage<NavigateToPaymentMessage>({
        action: Actions.NavigateToPayment,
      });
      browser_api.runtime.onMessage.removeListener(listener);
    }
  });
});

async function openExtensionUpdateWelcomePage(
  previousVersion: string,
): Promise<void> {
  try {
    const currentVersion = Number.parseInt(
      browser_api.runtime.getManifest().version.replace(/\./g, ''),
    );
    const prevVersion = Number.parseInt(previousVersion.replace(/\./g, ''));
    const data = await getSessionMetaApiData();

    const showOnUpdateVersion = Number.parseInt(
      data.showOnUpdateVersion?.replace(/\./g, '') ?? currentVersion.toString(),
    );

    const [{ apiBaseUrl }, { deviceId, deviceIp }, { userId, hasPremium }] =
      await Promise.all([
        getSessionMetaApiData(),
        getLocalStorageData([
          LocalStorageKey.DeviceId,
          LocalStorageKey.DeviceIp,
        ]),
        getLocalStoragePremiumData([
          LocalStoragePremiumKey.UserId,
          LocalStoragePremiumKey.HasPremium,
        ]),
      ]);

    const isCorrectVersion = showOnUpdateVersion > prevVersion;
    const isFreeUser = userId && deviceId && !hasPremium;

    if (process.env.ON_UPDATE_URL && isCorrectVersion && isFreeUser) {
      const response = await fetchCheckTariffAvailability(apiBaseUrl, {
        userId,
        deviceId,
        deviceIp,
        tariff: Tariff.FreeTrial,
      });

      if ('error' in response) {
        // TODO: replace error handling https://vpnn.youtrack.cloud/issue/FRO-552
        throw new Error(response.error);
      }
      const { status } = response;

      if (status === 'allowed') {
        const onUpdateUrl = await getConfigsUrl(process.env.ON_UPDATE_URL);

        if (onUpdateUrl) {
          await browser_api.tabs.create({ url: onUpdateUrl.toString() });
        }
      }
    }
  } catch (err) {
    console.error(err);
  }
}

browser_api.runtime.onInstalled.addListener(async (details) => {
  await browser_api.storage.session.clear();

  if (details.reason === 'install') {
    await getDeviceIP();
    await getDeviceId();

    await analytics.trackUserAction('install', 'extension');
    await openExtensionPopup();
  }

  if (details.reason === 'update') {
    await getDeviceIP();
    await getDeviceId();

    await analytics.trackUserAction(
      'update',
      'extension',
      details.previousVersion ?? '6.1.0',
    );
    await openExtensionUpdateWelcomePage(details.previousVersion ?? '6.1.0');
  }
});

browser_api.alarms.onAlarm.addListener(async (alarm) => {
  const name = alarm.name as PremiumAlarms;
  if (name === PremiumAlarms.ShowAdPremiumBanner) {
    const { hasPremium, status } = await getLocalStoragePremiumData([
      LocalStoragePremiumKey.HasPremium,
      LocalStoragePremiumKey.Status,
    ]);
    if (hasPremium && status === 'Paid') {
      await setLocalStoragePremiumData({ isAdBannerShown: false });
    } else {
      await setLocalStoragePremiumData({ isAdBannerShown: true });
    }
    await browser_api.alarms.clear(name);
  }
});

browser_api.runtime.onMessage.addListener(async (message: unknown) => {
  if (
    !isPremiumMessageOfType(message, PremiumActions.InitAdPremiumBanner) ||
    !isPremiumMessageOfType(message, PremiumActions.CloseAdPremiumBanner)
  )
    return;

  await browser_api.alarms.create(PremiumAlarms.ShowAdPremiumBanner, {
    periodInMinutes: 24 * 60,
  });
});

browser_api.storage.local.onChanged.addListener(
  async (changes: { hasPremium?: { newValue: boolean } }) => {
    if (changes.hasPremium) {
      await setLocalStoragePremiumData({
        isAdBannerShown: !changes.hasPremium.newValue,
      });
      if (changes.hasPremium.newValue) {
        await setLocalStorageData({
          onboardingScreenStatus: OnboardingStep.Done,
        });
      }
    }
  },
);

browser_api.storage.local.onChanged.addListener(
  async (changes: { userId?: { newValue: string } }) => {
    if (
      changes.userId &&
      (changes.userId.newValue === undefined || changes.userId.newValue === '')
    ) {
      await setLocalStorageData({ onboardingScreenStatus: 'step-zero' });
    }

    if (changes.userId && changes.userId.newValue) {
      await setUninstallUrl(changes.userId.newValue);
    }
  },
);

browser_api.webRequest.onBeforeRedirect.addListener(
  async (details) => {
    const isCaptchaUrl =
      details.redirectUrl.includes('google.com/sorry') &&
      details.url.includes('youtube.com/watch');

    const shouldSkipCaptcha = await checkShouldSkipCaptcha();

    if (!shouldSkipCaptcha && isCaptchaUrl && details.url && details.tabId) {
      const { currentState } = await getLocalStorageData(
        LocalStorageKey.CurrentState,
      );

      if (currentState === 'connected') {
        await redirectToVideo(details.url, details.tabId);
      }
    }
  },
  { urls: ['*://*.youtube.com/*'], types: ['main_frame'] },
  [],
);

async function init(): Promise<void> {
  try {
    const { isBrowserError } = await getLocalStorageData(
      LocalStorageKey.IsBrowserError,
    );
    if (isBrowserError) {
      await analytics.trackError('Browser error detected', 'initialization');
      void setLocalStorageData({
        currentState: 'disconnected',
        targetState: 'disconnected',
      });
    } else {
      await getSessionMetaApiData();

      const shouldAutostart = process.env.SHOULD_AUTOSTART === 'true';

      if (shouldAutostart) {
        await analytics.trackUserAction('autostart', 'proxy');
        await initProxy();
      } else {
        await analytics.trackUserAction('manual_start', 'proxy');
        void setLocalStorageData({
          currentState: 'disconnected',
        });
      }

      await setSessionStorageData({ isInitialized: true });

      await initExtensionMonitoring();
    }
  } catch (error) {
    console.error(error);
    await analytics.trackError(
      error instanceof Error ? error.message : 'Unknown error',
      'initialization',
    );
    await setLocalStorageData({ currentState: 'error' });
  }
}

// proxyController.fetchProxyData = getPremiumProxyData;

const { isInitialized } = await getSessionStorageData(
  SessionStorageKey.IsInitialized,
);

if (!isInitialized) {
  await init();
}
